import React, { FC, useState, useEffect } from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Grid from '@material-ui/core/Grid';
import InputLabel from '@material-ui/core/InputLabel';
import InputAdornment from '@material-ui/core/InputAdornment';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControl from '@material-ui/core/FormControl';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Typography from '@material-ui/core/Typography';
import { makeStyles, Theme } from '@material-ui/core/styles';
import clsx from 'clsx';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faMinusCircle } from '@fortawesome/free-solid-svg-icons';

import { Components } from '@hims/core';

import { IEditPlanTypeModal } from './IEditPlanTypeModal';

const InputTextField = Components.UI.InputTextField.InputTextField;

const useStyles = makeStyles((theme: Theme) => ({
	dialog: {},
	dialogAction: {
		marginBottom: theme.spacing(2),
		paddingTop: theme.spacing(4),
		justifyContent: 'center'
	},
	container: {
        paddingRight:'15px',
        paddingLeft:'15px',
        maxWidth: '500px'
	},
	buttons: {
		minWidth: 115,
		marginRight: '20px'
	},
	containergrid:{
		maxHeight: '60vh'
	}
}));

const comboStyles = makeStyles({
	root: {
		marginTop: '16px',
		marginBottom: '8px'
	},
	adornedEnd: {
		paddingRight: 0,
	}
});

const comboSelectStyles = makeStyles({
	notchedOutline: {
		borderTopLeftRadius: 0,
		borderBottomLeftRadius: 0,
	},
	input: {
		textTransform: 'uppercase',
		padding: '12.5px 16px',
	},
	root: {
		height: '2.90em'
	}
});

const textSelectStyles = makeStyles({
	label: {
		fontSize:'.8em'
	},
	text:{
        fontSize: '12px'
    },
});

//const numallowedpattern = new RegExp(/\D/);

export type IDataField = IEditPlanTypeModal.IDataField;

export const EditPlanTypeModal: FC<IEditPlanTypeModal.IProps> = props => {
	const classes = useStyles();
	const comboClasses = comboStyles();
	const comboSelectClasses = comboSelectStyles();
	const textClasses = textSelectStyles();

	const [name, setName] = useState<string>('');
	const [nameError, setNameError] = useState<boolean>(false);
	const [nameErrorMessage, setNameErrorMessage] = useState('');
	const [iPrincipal, setIPrincipal] = useState<string>('0');
	const [iPrincipalError, setIPrincipalError] = useState<boolean>(false);
	const [iDependent, setIDependent] = useState<string>('0');
	const [iDependentError, setIDependentError] = useState<boolean>(false);
	const [contractPlanType, setContractPlanType] = useState<string>('HMO');
	const [requestedRate, setRequestedRate] = useState<number>(0);
	const [formattedRequestedRate, setFormattedRequestedRate] = useState<string>('0');
	const [requestedRateError, setRequestedRateError] = useState<boolean>(false);
	const [adminFee, setAdminFee] = useState<number>(0);
	const [formattedAdminFee, setFormattedAdminFee] = useState<string>('0');
	const [adminFeeError, setAdminFeeError] = useState<boolean>(false);
	const [naf, setNAF] = useState<number>(0);
	const [formattedNAF, setFormattedNAF] = useState<string>('0');
	const [NAFError, setNAFError] = useState<boolean>(false);

	const trimByChar = (s: string, c: string) => {
		if (c === "]") c = "\\]";
		if (c === "\\") c = "\\\\";
		return s.replace(new RegExp(
		  "^[" + c + "]+$", "g"
		), "");
	};

	const getFormattedNumber = (val: number): string => {
		
		let frr = val.toString();

		if(val > 999){
			let wholeVal = Math.floor(val);
			let decimal = val - wholeVal;
			let wholeValStr = wholeVal.toString();
			
			let extraIndex = wholeValStr.length % 3;
			if(extraIndex === 0){
				let otherArr = wholeValStr.match(/.{1,3}/g);
				if(otherArr){
					frr = otherArr.join(',') + (decimal > 0 ? ('.' + decimal) : '');
				}
			}
			else{
				let start = wholeValStr.substring(0, extraIndex);
				let other = wholeValStr.substring(extraIndex);
				let otherArr = other.match(/.{1,3}/g);
				if(otherArr){
					frr = start + ',' + otherArr.join(',') + (decimal > 0 ? ('.' + decimal) : '');
				}
			}
		}

		return frr;
	};

	const onContractPlanTypeChange = (value: string) => {
		switch(value){
			case 'HMO':
				setAdminFee(0);
				setFormattedAdminFee('0');
				setAdminFeeError(false);

				setNAF(0);
				setFormattedNAF('0');
				setNAFError(false);
				break;
			case 'TPA':
				setRequestedRate(0);
				setFormattedRequestedRate('0');
				setRequestedRateError(false);
				break;
			default:
				break;
		}
		setContractPlanType(value);
	}

	const onAdminFeeChange = (value: string) => {
		value = value.replace(/ /g, '');
		value = value.replace(/,/g, '');
		value = trimByChar(value, '0');
		if(value.length===0){
			value = '0';
		}
		
		let val: number = parseFloat(value);
		if(isNaN(val)){
			val = 0;
		}
		
		let frr = getFormattedNumber(val);
		
		setAdminFee(val);
		setFormattedAdminFee(frr);
	};

	const onNAFChange = (value: string) => {
		value = value.replace(/ /g, '');
		value = value.replace(/,/g, '');
		value = trimByChar(value, '0');
		if(value.length===0){
			value = '0';
		}
		
		let val: number = parseFloat(value);
		if(isNaN(val)){
			val = 0;
		}
		
		let frr = getFormattedNumber(val);
		
		setNAF(val);
		setFormattedNAF(frr);
	};

	const onRenewalRateChange = (value: string) => {
		value = value.replace(/ /g, '');
		value = value.replace(/,/g, '');
		value = trimByChar(value, '0');
		if(value.length===0){
			value = '0';
		}
		
		let val: number = parseFloat(value);
		if(isNaN(val)){
			val = 0;
		}
		
		let frr = getFormattedNumber(val);
		
		setRequestedRate(val);
		setFormattedRequestedRate(frr);
	};

	useEffect(() => {
		if(props.benefitNode){
			if(props.benefitNode.name && props.benefitNode.name !== undefined){
				setName(props.benefitNode.name);
			}
			if(props.benefitNode.custom_metadata){
				if(props.benefitNode.custom_metadata.contract_plan_type 
					&& props.benefitNode.custom_metadata.contract_plan_type !== undefined){
						let contract_plan_type: string = props.benefitNode.custom_metadata.contract_plan_type;
						setContractPlanType(contract_plan_type);
				}
				if(props.benefitNode.custom_metadata.requested_rate 
					&& props.benefitNode.custom_metadata.requested_rate !== undefined){
						let req_rate: number = props.benefitNode.custom_metadata.requested_rate;
						let freq_rate: string = getFormattedNumber(req_rate);
						setRequestedRate(req_rate);
						setFormattedRequestedRate(freq_rate);
				}
				if(props.benefitNode.custom_metadata.admin_fee 
					&& props.benefitNode.custom_metadata.admin_fee !== undefined){
						let admin_fee: number = props.benefitNode.custom_metadata.admin_fee;
						let fadmin_fee: string = getFormattedNumber(admin_fee);
						setAdminFee(admin_fee);
						setFormattedAdminFee(fadmin_fee);
				}
				if(props.benefitNode.custom_metadata.naf 
					&& props.benefitNode.custom_metadata.naf !== undefined){
						let naf: number = props.benefitNode.custom_metadata.naf;
						let fnaf: string = getFormattedNumber(naf);
						setNAF(naf);
						setFormattedNAF(fnaf);
				}
				if(props.benefitNode.custom_metadata.indicative_principals 
					&& props.benefitNode.custom_metadata.indicative_principals !== undefined){
						setIPrincipal(props.benefitNode.custom_metadata.indicative_principals);
				}
				if(props.benefitNode.custom_metadata.indicative_dependents 
					&& props.benefitNode.custom_metadata.indicative_dependents !== undefined){
						setIDependent(props.benefitNode.custom_metadata.indicative_dependents);
				}
			}
		}
	}, [props.benefitNode]);

	const reset = () => {
		setName('');
		setNameError(false);
		setIPrincipal('0');
		setIPrincipalError(false);
		setIDependent('0');
		setIDependentError(false);
		setContractPlanType('HMO');
		setRequestedRate(0);
		setFormattedRequestedRate('0');
		setRequestedRateError(false);
		setAdminFee(0);
		setFormattedAdminFee('0');
		setAdminFeeError(false);
		setNAF(0);
		setFormattedNAF('0');
		setNAFError(false);
		// setCode('');
		// setCodeError(false);
	};

	const handleClose = () => {
		reset();
		props.onClose();
	};

	const handleSubmit = () => {
		let error = false;
		if (name.length === 0) {
			setNameError(true);
			error = true;
		}
		const index = props.planTypeList.findIndex(item => 
		    item.id !== props.plan_type_id && item.name.toLowerCase() === name.toLowerCase()
		);
		if(index !== undefined && index !== -1){
			setNameError(true);
			setNameErrorMessage("Name already exists.");
			error = true;
		}

		if (parseFloat(iPrincipal) < 0) {
			setIPrincipalError(true);
			error = true;
		}

		if (parseFloat(iDependent) < 0) {
			setIDependentError(true);
			error = true;
		}

		if(contractPlanType === 'HMO'){
			if ( requestedRate <= 0 ) {
				setRequestedRateError(true);
				error = true;
			}
			else{
				setRequestedRateError(false);
			}
		}
		else if(contractPlanType === 'TPA'){
			if ( adminFee <= 0 ) {
				setAdminFeeError(true);
				error = true;
			}
			else{
				setAdminFeeError(false);
			}
			if ( naf <= 0 ) {
				setNAFError(true);
				error = true;
			}
			else{
				setNAFError(false);
			}
		}

		if (!error) {
			/*
			switch(contractPlanType){
				case 'HMO':
					setAdminFee(0);
					setFormattedAdminFee('0');
					setAdminFeeError(false);
	
					setNAF(0);
					setFormattedNAF('0');
					setNAFError(false);
					break;
				case 'TPA':
					setRequestedRate(0);
					setFormattedRequestedRate('0');
					setRequestedRateError(false);
					break;
				default:
					break;
			}
			*/

			const data: IDataField = {
				name: name,
				title: "Benefits for " + name,
				indicative_principals: parseFloat(iPrincipal),
				indicative_dependents: parseFloat(iDependent),
				contract_plan_type: contractPlanType,
				requested_rate: requestedRate,
				admin_fee: adminFee,
				naf: naf,
			};

			reset();
			props.onSubmit(data, props.plan_type_id, props.plan_type_name);
			props.onClose();
		}
	};

	return (
		<Dialog
			open={props.isModalOpen}
			onClose={handleClose}
			aria-labelledby="form-dialog-title"
			maxWidth="sm"
			className={classes.dialog}
		>
			<div>
                <Grid container className={classes.container} justify="flex-start" alignItems="flex-start" direction="column">
                    <Grid item xs>
						<DialogTitle id="form-dialog-title">Edit Member Plan Type</DialogTitle>
					</Grid>
					<Grid item xs>
						<DialogContent>
							<Grid container className={classes.containergrid} spacing={2} justify="space-between" alignItems="flex-start">
								<Grid item xs={12}>
									<InputTextField
										label="Plan Type Name*"
										value={name}
										error={nameError}
										errorMessage={nameErrorMessage}
										onChange={(value: string) => {
											setName(value);
											setNameError(value.length === 0);
											setNameErrorMessage('');
										}}
									/>
								</Grid>
								{/*<Grid item xs={12}>
									<InputTextField
										label="Plan Type Code"
										value={code}
										error={codeError}
										onChange={(value: string) => {
											setCode(value);
											setCodeError(value.length === 0 || value === 'BASE');
										}}
									/>
								</Grid>*/}
								<Grid item xs={6}>
									<InputTextField
										type="number"
										label="Indicative No. of Principals"
										value={iPrincipal}
										error={iPrincipalError}
										onChange={(value: string) => {
											setIPrincipal(value);
											setIPrincipalError(parseInt(value) < 0);
										}}
									/>
								</Grid>
								<Grid item xs={6}>
									<InputTextField
										type="number"
										label="Indicative No. of Dependents"
										value={iDependent}
										error={iDependentError}
										onChange={(value: string) => {
											setIDependent(value);
											setIDependentError(parseInt(value) < 0);
										}}
									/>
								</Grid>
								{/* <Grid item xs={6}>
									<InputTextField
										type="tel"
										label="Requested Rate*"
										value={formattedRequestedRate}
										error={requestedRateError}
										onChange={onRenewalRateChange}
									/>
								</Grid> */}
								<Grid item xs={12}>
									<React.Fragment>
										<InputLabel 
											htmlFor="contract_plan_types"
											className={textClasses.label}
										>
											Contract Plan Type*
										</InputLabel>
										<FormControl component="fieldset" margin="normal">
											<RadioGroup name="contract_plan_types">
												<FormControlLabel
													value="HMO"
													control={<Radio 
														style={{padding:'5px'}}
														checked={contractPlanType === 'HMO'} 
														name="contract_plan_types"
														onClick={(e) => {
															e.stopPropagation();
															onContractPlanTypeChange('HMO');
														}}
													/>}
													label={<Typography className={textClasses.text}>HMO</Typography>}
													// control={
													// 	<Radio 
													// 		checked={contractPlanType === 'HMO'} 
													// 		name="contract_plan_types"
													// 		onClick={(e) => {
													// 			e.stopPropagation();
													// 			onContractPlanTypeChange('HMO');
													// 		}}
													// 	/>
													// }
													// label={'HMO'}
												/>
												<FormControlLabel
													value="TPA"
													control={<Radio 
														style={{padding:'5px'}}
														checked={contractPlanType === 'TPA'} 
														name="contract_plan_types"
														onClick={(e) => {
															e.stopPropagation();
															onContractPlanTypeChange('TPA');
														}}
													/>}
													label={<Typography className={textClasses.text}>TPA</Typography>}
												/>
											</RadioGroup>
										</FormControl>
									</React.Fragment>
								</Grid>

								{
									contractPlanType === 'HMO' ?
									<>
										<Grid item xs={6}>
											<InputLabel htmlFor={'requestedRate'} className={textClasses.label}>Requested Rate*</InputLabel>
											<OutlinedInput
												type="tel"
												name={'requestedRate'}
												classes={comboClasses}
												value={formattedRequestedRate}
												error={requestedRateError}
												onChange={e => {
													onRenewalRateChange(e.target.value);
												}}
												endAdornment = {(
													<InputAdornment position="end">
														<OutlinedInput
															classes={comboSelectClasses}
															name={'requestedRate_adornment'}
															disabled={true}
															value={'PHP'}
															labelWidth={0}
															id={'requestedRate_adornment'} />
													</InputAdornment>
												)}
												labelWidth={0}
											/>
										</Grid>
										<Grid item xs={6}></Grid>
									</> : ''
								}
								
								{
									contractPlanType === 'TPA' ?
									<>
										<Grid item xs={6}>
											<InputLabel htmlFor={'adminFee'} className={textClasses.label}>Admin Fee*</InputLabel>
											<OutlinedInput
												type="tel"
												classes={comboClasses}
												value={formattedAdminFee}
												error={adminFeeError}
												onChange={e => {
													onAdminFeeChange(e.target.value);
												}}
												endAdornment = {(
													<InputAdornment position="end">
														<OutlinedInput
															classes={comboSelectClasses}
															name={'adminFeeCurrency'}
															disabled={true}
															value={'PHP'}
															labelWidth={0}
															id={'adminFee_adornment'} />
													</InputAdornment>
												)}
												labelWidth={0}
											/>
										</Grid>
										<Grid item xs={6}>
											<InputLabel htmlFor={'naf'}
												className={textClasses.label}>NAF*</InputLabel>
											<OutlinedInput
												type="tel"
												classes={comboClasses}
												value={formattedNAF}
												error={NAFError}
												onChange={e => {
													onNAFChange(e.target.value);
												}}
												endAdornment = {(
													<InputAdornment position="end">
														<OutlinedInput
															classes={comboSelectClasses}
															name={'NAFPercent'}
															disabled={true}
															value={'%'}
															labelWidth={0}
															id={'NAF_adornment'} />
													</InputAdornment>
												)}
												labelWidth={0}
											/>
										</Grid>
									</> : ''
								}
								<Grid item xs={12} style={{paddingTop: '15px'}}>
									<span onClick={props.onDelete}>
										<FontAwesomeIcon
											className="fa-sm"
											icon={faMinusCircle}
											style={{ color: '#9EA3AC', fontSize: '15px', marginRight:'5px'}}
										/> Delete this Plan Type
									</span>
								</Grid>
							</Grid>
						</DialogContent>
						<DialogActions className={clsx(classes.dialogAction)}>
								<Button onClick={handleClose} variant="outlined" color="default" className={classes.buttons}>
									Cancel
								</Button>
								<Button
									onClick={handleSubmit}
									variant="contained"
									color="primary"
									className={classes.buttons}
								>
									Save
								</Button>
						</DialogActions>
					</Grid>
				</Grid>
			</div>
		</Dialog>
	);
};

EditPlanTypeModal.defaultProps = {
	isModalOpen: false,
};
