type IComboFieldProps = TextFieldProps | OutlinedTextFieldProps;

export interface IComboContainerProps {
	fieldValue?: string;
	selectValue?: string;
	selectName?: string;
	fieldName?: string;
	disableSelect?: boolean;
	noDefault?: boolean;
	inputProps?: any;
	fieldOnChange?: (e: any) => void;
	selectOnChange?: (e: any) => void;
}


export interface IComboMainFieldProps {
	items: any;
	defaultValue: string;
	type: string;
	currency: boolean;
	givenInputProps?: any;
}

export const EligibleMembersField: React.FC<IComboFieldProps & IComboContainerProps> = (props: IComboFieldProps & IComboContainerProps): JSX.Element => {
	const items = [{ name: 'Named', value: 'named' }, { name: 'Max', value: 'max' }];

	return (
		<ComboField
			{...props}
			items={items}
			type={'number'}
			currency={false}
			defaultValue={'named'}
		/>
	);
};

export const CurrencyField: React.FC<IComboFieldProps & IComboContainerProps> = (props: IComboFieldProps & IComboContainerProps): JSX.Element => {
	const items = [
		{ name: 'USD', value: 'USD' },
		{ name: 'PHP', value: 'PHP' },
		{ name: 'EUR', value: 'EUR' },
	];

	return (
		<ComboField
			{...props}
			items={items}
			type={'number'}
			currency={true}
			defaultValue={'PHP'}
		/>
	);
};

export const LimitField: React.FC<IComboFieldProps & IComboContainerProps> = (props: IComboFieldProps & IComboContainerProps): JSX.Element => {
	const items = [
		{ name: 'Amount Limit', value: 'amt_limit' },
		{ name: 'Max Session', value: 'max_session' },
		{ name: 'Max Amount', value: 'max_amount' },
		{ name: 'Percent of MBL', value: 'percent_mbl' },
		{ name: 'Percent of ABL', value: 'percent_abl' },
		{ name: 'No. of Visits', value: 'no_visits' },
	];

	return (
		<ComboField
			{...props}
			items={items}
			type={'number'}
			currency={(props.selectValue === 'amt_limit' || props.selectValue === 'max_amount')}
			defaultValue={'percent_mbl'}
		/>
	);
};


