const ComboField: React.FC<IComboFieldProps & IComboMainFieldProps & IComboContainerProps> = (
	props: IComboFieldProps & IComboMainFieldProps & IComboContainerProps,
): JSX.Element => {
	const comboClasses = comboStyles();
	const comboSelectClasses = comboSelectStyles();

	let id: string | undefined;
	let label: React.ReactNode;
	let textfieldProps: any;
	let items: any;
	let fieldName: string | undefined;
	let selectName: string | undefined;
	let defaultValue: string;
	let fieldValue:  string | undefined;
	let selectValue:  string | undefined;
	//let type: string;
	let fieldOnChange: any;
	let selectOnChange: any;
	let disableSelect: boolean | undefined;
	let inputProps: any;
	//let currency : boolean;
  	//const [blankValue, setBlankValue] = React.useState(false);
	const {noDefault} = props;
	const {} = props;
	({ id, label, items, fieldName, selectName, fieldOnChange, selectOnChange, fieldValue, selectValue, defaultValue, disableSelect, inputProps, ...textfieldProps } = props);
	//const numallowedpattern = new RegExp(/\D/);
	//const givmin = {min:'0'};

	if (inputProps === undefined) {
		inputProps = {};
	}

	if (noDefault) {
		defaultValue = 'none';
	}


	//let value: any = '';

	/*if (fieldValue) {
		if (currency) {
			value = fieldValue;
		} else {
			value = fieldValue;
		}
	} else {
		if (type==='tel') {
			value = '0'
		} else if (type ==='number') {
			value = '';
		} else {
			value = ''
		}
	}*/

	let endadornment:any = null;
	if(items){
		if(items.length === 1){
			endadornment = (
				<OutlinedInput
					classes={comboSelectClasses}
					name={selectName}
					disabled={true}
					value={items[0].name}
					labelWidth={0}
					id={id + '_adornment'} />
			);
		}
		else{
			endadornment = (
				<Select
					value={selectValue ? selectValue : defaultValue}
					onChange={
						(e) => { 
									selectOnChange(e);
								}
						}
					disabled={disableSelect ? disableSelect : false}
					input={
						<OutlinedInput
							classes={comboSelectClasses}
							name={selectName}
							labelWidth={0}
							id={id + '_adornment'}
						/>
					}
				>
					{noDefault ? <MenuItem selected disabled value="none">
							
					</MenuItem> : null}
					{items.map((item: any) => (
						<MenuItem key={item.value} value={item.value}>
							{item.name}
						</MenuItem>
					))}
				</Select>
			);
		}
	}
	else{
		endadornment = ''; 
	}

	/*const numformat = () => {
		return (
			<NumberFormat
			thousandSeparator={true}
			value={fieldValue}
			customInput={OutlinedInput}
			InputProps={{classes:comboClasses,
				style:[{borderTopRightRadius:0,width:'350px',borderBottomRightRadius:0}]}} 
			/>
		)
	}*/
 
	/*const limitInput = () => {
		return (<OutlinedInput 
			id={id}
			classes={comboClasses}
			value={fieldValue}
			endAdornment = {(
				<InputAdornment position="end">
					{ endadornment }
				</InputAdornment>
			)}
			{...textfieldProps}
		/>)
	}*/

	/*const endAdornment = () => {
		return (
					<InputAdornment position="end">
						{ endadornment }
					</InputAdornment>
				)
	}*/

	/*const handleChange = () => event => {
		console.log('event',event)
		if (fieldOnChange) {
			fieldOnChange(event);
		}
	}*/

	const [tempvalue,setTempValue] = React.useState<any>(null)
	// const [focused,setFocused] = React.useState(false)

	function NumberFormatCustom(props) {
		const { inputRef, onChange, ...other } = props;
	  
		return (
		  <NumberFormat
			{...other}
			getInputRef={inputRef}
			classes={comboClasses}
			value={fieldValue ? fieldValue : tempvalue}
			name={fieldName}
			onValueChange={(e)=>{
				// if(!focused) {
				// 	// setTempValue(fieldValue ? e.value : null);
				// 	setFocused(true)
				// }
				console.log('onChange', e);
			}}
			onBlur={(e)=>{
				// setFocused(false)
				if (fieldOnChange) {
					fieldOnChange(e);
					setTempValue(e.target.value);
				}
				console.log('BLUR', e.target.value);
			}}
			onFocus={(e)=>{
				e.target.click();
			}}
			thousandSeparator
			isNumericString
		  />
		);
	  }
	  
	  NumberFormatCustom.propTypes = {
		inputRef: PropTypes.func.isRequired,
		onChange: PropTypes.func.isRequired,
	  };
	  
	
	return (
		<React.Fragment>
			<InputLabel htmlFor={id + '_label'}>{label}</InputLabel>
			<OutlinedInput
				labelWidth={0}
				classes={comboClasses}
				style={{outlineOffset:'-2px',
						outlineColor:'#3AB77D'}}
				value={fieldValue}
				
				id="formatted-text-mask-input"
				inputComponent={NumberFormatCustom}
				endAdornment = {(
					<InputAdornment position="end">
						{ endadornment }
					</InputAdornment>
				)}
			/>
			{/*<OutlinedInput
				id={id}
				classes={comboClasses}
				type={currency ? 'tel' : type === 'number' ? 'tel' : type}
				name={fieldName}
				value={fieldValue}
				inputComponent={NumberFormatCustom}
				onChange={(e) => {
					if (fieldOnChange) {
						console.log('check')
						fieldOnChange(e);
					}
				}}
				endAdornment = {(
					<InputAdornment position="end">
						{ endadornment }
					</InputAdornment>
				)}
				{...textfieldProps}
			/>*/}
		</React.Fragment>
	);
};
