import * as React from 'react';
import IconButton from '@material-ui/core/IconButton';
import Tab, { TabProps } from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPencilAlt } from '@fortawesome/free-solid-svg-icons';

type PlanTypeTabProps = TabProps & {
	viewOnly?: boolean;
	subtitle?: string;
	data?: any;
	benefit_id: string;
	name: string;
	onEdit: (id:string, name:string) => void;
};

export const PlanTypeTab: React.FC<PlanTypeTabProps> = (props: PlanTypeTabProps): JSX.Element => {
	const { label, subtitle, ...tabProps } = props;

	const getFormattedNumber = (val: number): string => {
		
		let frr = val.toString();

		if(val > 999){
			let wholeVal = Math.floor(val);
			let decimal = val - wholeVal;
			let wholeValStr = wholeVal.toString();
			
			let extraIndex = wholeValStr.length % 3;
			if(extraIndex === 0){
				let otherArr = wholeValStr.match(/.{1,3}/g);
				if(otherArr){
					frr = otherArr.join(',') + (decimal > 0 ? ('.' + decimal) : '');
				}
			}
			else{
				let start = wholeValStr.substring(0, extraIndex);
				let other = wholeValStr.substring(extraIndex);
				let otherArr = other.match(/.{1,3}/g);
				if(otherArr){
					frr = start + ',' + otherArr.join(',') + (decimal > 0 ? ('.' + decimal) : '');
				}
			}
		}

		return frr;
	};
	
	const handleEdit= () =>{
		props.onEdit(props.benefit_id, props.name);
	}
	return (
		<Tab
			label={
				<div>
					<Typography className="tab_text" variant="h6">
						{label}
						<IconButton size="small"></IconButton>
						{ !props.viewOnly && props.value !== "BASE" ?
							<span className="edit_btn" style={{position: "relative", top: 0, right: 0}} onClick={handleEdit}>
								<FontAwesomeIcon
									className="fa-sm"
									icon={faPencilAlt}
									style={{ color: '#3AB77D' }}
								/>
							</span>: null
						}
					</Typography>
					{subtitle ? (
						<Typography className="tab_caption" variant="caption">
							{subtitle}
						</Typography>
					) : null}
					<br/>
					{ props.data !== undefined && (props.data["contract_plan_type"] === 'HMO' || (props.data["requested_rate"] !== undefined 
						&& props.data["requested_rate"] !== 0)) ?
						<Typography className="tab_caption" variant="caption">
							{getFormattedNumber(props.data["requested_rate"] ? props.data["requested_rate"] : 0) + " PHP"}
						</Typography>: null

					}
					{ props.data !== undefined && props.data["contract_plan_type"] === 'TPA' ?
						<>
							<Typography className="tab_caption" variant="caption">
								{getFormattedNumber(props.data["admin_fee"] ? props.data["admin_fee"] : 0) + " PHP"}
							</Typography>
							<br/>
							<Typography className="tab_caption" variant="caption">
								{(props.data["naf"] ? props.data["naf"] : '0') + "%"}
							</Typography>
						</> : null
					}
				</div>
			}
			{...tabProps}
		/>
	);
};
